#book-logo {
  font-family: 'Bebas Neue', cursive;
  color: rgb(72, 82, 138);
}
.section-border {
  border: 1px solid hsl(0, 0%, 96%);
  border-radius: 0.5rem;
  padding: 2rem;
}

.topPad {
  padding-bottom: 1rem;
}
.botPad {
  padding-top: 1rem;
}

#search-btn {
  width: 100%;
  background-color: rgb(72, 82, 138);
  font-weight: bold;
  letter-spacing: 0.5px;
  color: white;
}

#input {
  width: 90%;
}
.marg-bot {
  margin-bottom: 0.5rem;
}
.view-btn {
  background-color: rgb(72, 82, 138);
}
.save-btn,
.view-btn,
.delete-btn {
  height: auto;
  width: 35px;
  padding-bottom: 5px;
  padding-top: 5px;
  padding-left: 7px;
  padding-right: 7px;
}
.fa-heart,
.fa-times {
  color: rgb(72, 82, 138);
}

#footer {
  background-color: rgb(72, 82, 138);
  color: white;
}
